.page-footer {
  width: 100%;
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.page-footer .share {
  text-align: right;
  margin-bottom: 24px;
}
.page-footer .share .wx-icon {
  font-size: 40px;
  display: inline-block;
}
.page-footer .share .wx-icon:hover .qrcode {
  display: block;
}
.page-footer .share .wx-icon:hover .qrcode .arrow::before {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.07);
  transform: translateY(-6.53553391px) rotate(45deg);
  display: block;
  width: 5px;
  height: 5px;
  margin: auto;
  background-color: rgba(0, 0, 0, 0.75);
  content: '';
  pointer-events: auto;
}
.page-footer .share .qrcode {
  font-size: 14px;
  position: absolute;
  display: none;
  top: -162px;
  left: -51px;
  z-index: 999;
}
.page-footer .share .arrow {
  left: 50%;
  transform: translateX(-50%);
  bottom: -13.07106781px;
  position: absolute;
  width: 13.07106781px;
  background: transparent;
  height: 13.07106781px;
}
.page-footer .share .inner {
  padding: 6px 8px;
  color: #fff;
  text-align: left;
  background-color: rgba(0, 0, 0, 0.75);
  border-radius: 2px;
  box-shadow: 0 3px 6px -4px rgba(0, 0, 0, 0.12), 0 6px 16px 0 rgba(0, 0, 0, 0.08), 0 9px 28px 8px rgba(0, 0, 0, 0.05);
}
.page-footer .share .react-share__ShareButton:focus {
  outline: none;
}
.page-footer .content {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border-top: 1px solid #e8e8e8;
  border-bottom: 1px solid #e8e8e8;
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
}
.page-footer .content .prev,
.page-footer .content .next {
  width: 50%;
  padding: 32px 12px;
}
.page-footer .content .prev a,
.page-footer .content .next a {
  display: block;
}
.page-footer .content .prev .label,
.page-footer .content .next .label {
  margin-bottom: 8px;
  font-size: 14px;
  color: #bfbfbf;
}
.page-footer .content .prev .title,
.page-footer .content .next .title {
  font-size: 16px;
  font-weight: 500;
  color: #262626;
  transition: color 0.35s ease;
}
.page-footer .content .next {
  text-align: right;
}
.page-footer .books .book {
  display: flex;
  position: relative;
  background: #fafafa;
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  padding: 32px 16px 24px;
  margin-bottom: 24px;
  justify-content: space-between;
  align-items: center;
}
.page-footer .books .book .from {
  position: absolute;
  top: 12px;
  left: 12px;
  font-size: 12px;
  color: #bfbfbf;
}
.page-footer .books .book .icon {
  align-self: flex-start;
  padding-top: 6px;
  margin-right: 16px;
}
.page-footer .books .book .name {
  width: 100%;
  line-height: 1.5;
}
.page-footer .books .book .name a {
  color: #262626;
  font-size: 16px;
}
.page-footer .connect {
  margin-top: 40;
}
.page-footer .connect .othersites {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
.page-footer .connect .othersites a {
  margin: 0 15px;
  font-size: 32px;
  display: flex;
  align-items: center;
}
.page-footer .connect .author {
  display: flex;
  align-items: flex-end;
}
.page-footer .connect .author-content {
  width: calc(100% + 48px);
  text-align: center;
  padding: 24px 0;
  margin: 0 -24px;
  background: #94db9a;
  color: #fff;
}
.page-footer .connect .author-content a {
  font-size: 24px;
  color: #000;
}
